.restaurant {
	padding-bottom: 73px;
	&_karaoke {
		background-color: $black;
		.subtitle-md,
		.title-sm,
		.title-lg {
			color: $white; } }
	@include sm {
		padding-bottom: 27px; }
	&__article {
		margin-top: 73px;
		@include sm {
			margin-top: 27px; } }
	&__top {
		padding: 314px 0 38px;
		background-size: cover;
		background-position: 50% 50%;
		background-repeat: no-repeat; }
	&__main {
		padding-bottom: 75px;
		@include sm {
			padding-bottom: 27px; } }
	&__title-sm {
		text-align: center; }
	&__title-lg {
		text-align: center; } }
.banner {
	margin-top: 73px;
	padding: 245px 0;
	text-align: center;
	background-size: cover;
	background-position: 50% 50%;
	background-repeat: no-repeat;
	@include sm {
		padding: 90px 0;
		margin-top: 27px; } }
